const Class = require('cify');
const Observer = require('../observer');
const EventEmitter = require('../events');
const Compiler = require('./compiler');

/**
 * 
 *  element 
 */
const Template = new Class({

  $extends: EventEmitter,

  /**
   * 
   * @param {HTMLNode} element HTML 
   * @param {Object} options 
   * @returns {void} 
   */
  constructor: function (element, options) {
    options = options || {};
    EventEmitter.call(this);
    this.options = options;
    this.element = element;
    this.compiler = options.compiler || new Compiler(options);
    this.render = this.compiler.compile(this.element);
    this.update = this.update.bind(this);
    this._update = this._update.bind(this);
    this._updateTimer = 0;
  },

  /**
   *  ()
   * @returns {void} 
   */
  update: function () {
    if (this._updateTimer) {
      clearTimeout(this._updateTimer);
      this._updateTimer = null;
    }
    this._updateTimer = setTimeout(this._update, 0);
  },

  /**
   *  
   * @returns {void} 
   */
  _update: function () {
    if (!this._updateTimer || !this.observer) return;
    this.emit('update', this);
    this.render(this.observer.target);
    this._onBind();
  },

  /**
   * 
   * @returns {void} 
   */
  _onBind: function () {
    if (this._bound) return;
    this._bound = true;
    this.emit('bind', this);
  },

  /**
   *  scope
   * @param {Object} scope 
   * @param {boolean} disableFirst 
   * @returns {void} 
   */
  bind: function (scope, disableFirst) {
    if (!scope) return;
    this.unbind();
    this.observer = new Observer(scope, {
      root: this.options.root
    });
    scope.$self = scope;
    this.observer.on('change', this.update);
    if (disableFirst) {
      this._onBind();
    } else {
      this.update();
    }
  },

  /**
   * 
   * @returns {void} 
   */
  unbind: function () {
    if (!this.observer) return;
    this.observer.removeListener('change', this.update);
    this.observer.clearReference();
    this.observer = null;
  },

  /**
   * 
   * @returns {void} 
   */
  dispose: function () {
    this.unbind();
    this.render.dispose();
  }

});

module.exports = Template;


